import React from 'react';
import CustomButton from './Btn';


export default {
    title: 'atoms/CustomButton',
    component: CustomButton,
    argTypes:{
        color: {
            control: {
              type: 'radio',
              options: ['primary', 'secondary']
            }
          }
        }
};

const Template = (args) => <CustomButton {...args} />;

export const Primary = Template.bind({});;

Primary.args = {
   color: "primary",
    variant: "contained",
   label:"btn label"
   
};

export const Secondary = Template.bind({});

Secondary.args = {
   color: "secondary",
    variant: "contained",
   label:"btn label"
   
};


Primary.parameters = { storySource: { source: "(args) => <CustomButton {...args} />" }, ...Primary.parameters };
Secondary.parameters = { storySource: { source: "(args) => <CustomButton {...args} />" }, ...Secondary.parameters };